import {useState} from 'react'

export default function CadGame() {
    const [dados, setDados] = useState({
        categoria : "",
        nome : ""
    })

    function handleChange({target}) {
        const {id, value} = target
        setDados({...dados, [id] : value})
        console.log(dados)
    }

    function handleSubmit(event) {
        event.preventDefault()
        fetch('http://localhost:4000/cad', {
            method : "POST",
            headers : {"Content-Type" : "application/json"},
            body : JSON.stringify(dados)
        })
    }

    return (
        <form onSubmit={handleSubmit}>
            <label htmlFor="categoria">Categoria </label>
            <select id="categoria" onChange={handleChange} name="categoria">
                <option value="">selecione</option>
                <option value="fps">FPS</option>
                <option value="familia">Familia</option>
                <option value="aventura">Aventura</option>
                <option value="esportes">Esportes</option>
            </select>
            <input type="text" name="nome" id="nome" onChange={handleChange} placeholder="Insira o nome do jogo" />
            <input type="submit" value="cadastrar" />
        </form>
    )
}